import React from "react"
import { Link } from "gatsby"

import { rhythm, scale } from "../utils/typography"

const Layout = ({ location, title, children, isIndex}) => {
  const rootPath = `${__PATH_PREFIX__}/`
  let header

  if (location.pathname === rootPath) {
    header = (
      <div>
      <h1
        style={{
          ...scale(1.5),
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
      <p
        style={{
          marginTop: -20,
        }}
      >
        By <strong>NowhereMan</strong> who goes nowhere.
      </p>
      <hr
      style={{
        border: "none",
        height: "0.1px",
        backgroundColor: "#CDCDCD",
      }}
      />
      </div>
    )
  } else {
    header = (
      <h3
        style={{
          fontFamily: `Montserrat, sans-serif`,
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }
  const width = isIndex ? 48:28
  return (
    <div
      style={{
        marginLeft: `auto`,
        marginRight: `auto`,
        maxWidth: `${rhythm(width)}`,
        padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
      }}
    >
      <header>{header}</header>
      <main>{children}</main>
      <hr
      style={{
        border: "none",
        height: "0.1px",
        backgroundColor: "#CDCDCD",
      }}
      />
      <footer>
        <div
        style={{
          textAlign: "center"
        }}>
         {new Date().getFullYear()}, NowhereLog by NowhereMan
        </div>
      </footer>
    </div>
  )
}

export default Layout
